@font-face {
  font-family: 'RobotoMono';
  src: url(./assets/RobotoMono-Regular.ttf);
}

body{
  margin: 0;
  padding: 0;
  color: #242424;
  font-family: 'RobotoMono';
  word-wrap: break-word;
  overflow-wrap: break-word;
}

h1,h2,h3,h4,h5,h6{
  font-weight: 900;
  color:#000;
}

.wrapper{
  width: 100%;
  margin: 0 auto;
}

main{
  display: block;
  background-color: #000;
}

main p{
  font-size: 20px;
}

.container {
  width: 100%;
  max-width: 720px;
  margin: 0 auto;
  padding: 0 20px;
}

section.module{
  margin-bottom: 40px;
}

section.module:last-child{
  margin-bottom: 0;
}

section.module h2{
  margin-bottom: 40px;
  font-size: 30px;
}

section.module.content1{
  padding: 40px 0;
  background: #00bd1c;
}
section.module.content2{
  padding: 40px 0;
  background: #00bd1c;
}
section.module.content3{
  padding: 40px 0;
  background: #00bd1c;
}

section.module.parallax{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh !important;
  width: 100%;
  position: relative;
  overflow: hidden;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover; 
  -o-background-size: cover;
   
}
section.module.parallax:after{
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 0;
}
section.module.parallax{
  display: flex;
  justify-content: center;
  align-items: center;
}

section.module.parallax img{
  margin-top: 40px;
}

section.module.parallax-1{
  background-image: url("./assets/dark-wallpaper-20072813293931.jpg");
}
section.module.parallax-2{
  background-image: url("./assets/dark-wallpaper-20072813293931.jpg");
}
section.module.parallax-3{
  background-image: url("./assets/dark-wallpaper-20072813293931.jpg");
}
section.module.parallax-4{
  background-image: url("./assets/dark-wallpaper-20072813293931.jpg");
}


section.module.parallax h1{
  color: #00bd1c;
  text-align: center;
  font-size: 80px;
  z-index: 50;
}


footer{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #00bd1c;
}

footer h2{
   font-size: 20px;
   justify-content: center;
   padding-bottom: 5px;
}

.contatos p{
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: bold;
}
.links{
  color: #000;
}

.links:hover{
    color: white;
}

.botaocontainer{
  background-color: #00bd1c;
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
}

.botao{
  padding: 18px 30px 20px 30px;
  color: #000;
  font-size: 20px;
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
}

.botao:first-child{
  border-left: 0px;
}

.botao:last-child{
  border-right: 0px;
}
.botao:hover{
  zoom: 108%;
  color: white;
}




@media(max-width:765px) {
  .barra{
    width: 100%;
    background-color: #00bd1c;
  }
  .wrapper{
    width: 800px;
  }
  .footer{
    width: 800px;
  }
  .barracontainer{
    margin-right: 0;
    padding-right: 0;
    width: 100%;
    background-color: #00bd1c;
  }
}

